import React, { useEffect, useRef, useState } from "react";
import Webcam from "react-webcam";
import "./Reader.css";
import axios from "axios";

const videoConstraints = {
    width: 640,
    height: 480,
    facingMode: "user"
};

function Reader(props) {
    const videoRef = useRef();
    const webcamRef = useRef();
    const canvasRef = useRef();
    const [result, setResult] = useState();

    useEffect(
        function () {
            if (result) {
                props.onScan(result.data);
                setResult(null);
            }
        },
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [result]
    );

    useEffect(function () {
        let isMounted = true;
        console.log("Set interval");
        const inter = setInterval(() => {
            if (!isMounted)
            {
                console.log("Unmounted reader call");
                return;
            }
            requestAnimationFrame(tick);
        }, 2000);
        return () => {
            console.log("Clear interval for reader");
            isMounted = false;
            clearInterval(inter);
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(
        function () {
            navigator.mediaDevices
                .getUserMedia({ video: { facingMode: "environment" } })
                .then(function (stream) {
                    console.log(stream);
                    console.log("TEST");
                    try {
                        videoRef.current.srcObject = stream;
                        videoRef.current.setAttribute("playsinline", true); // required to tell iOS safari we don't want fullscreen
                        videoRef.current.play();
                    } catch {}
                });
        },
        [videoRef, canvasRef]
    );
    const drawLine = (context, info, style = {}) => {
        const { x, y, x1, y1 } = info;
        const { color = 'black', width = 1 } = style;

        context.beginPath();
        context.moveTo(x, y);
        context.lineTo(x1, y1);
        context.strokeStyle = color;
        context.lineWidth = width;
        context.stroke();
    }

/*
    var sendBase64ToServer = function(base64) {
        var httpPost = new XMLHttpRequest(),
        path = "http://127.0.0.1:8080/uploadImage",
        data = JSON.stringify({image: base64});
        httpPost.onreadystatechange = function(err) {
            if (httpPost.readyState == 4 && httpPost.status == 200){
                console.log(httpPost.responseText);
            } else {
                console.log(err);
            }
        };
        // Set the content type of the request to json since that's what's being sent
        httpPost.setHeader('Content-Type', 'application/json');
        httpPost.open("POST", path, true);
        httpPost.send(data);
    };
    var uploadImage = function(data, type){
        sendBase64ToServer(data);
    };
*/

    function tick() {
            canvasRef.current.height = 480; //videoRef.current.videoHeight;
            canvasRef.current.width = 640; //videoRef.current.videoWidth;

            var ctx = canvasRef.current.getContext("2d");
//            var ctx = canvasRef.current.getContext("2d");

            console.log('tick');

            const image = webcamRef.current.getScreenshot();
/*
            if (image) {
                const str = image.toDataURL();
                console.log(str);
            }
*/
//            uploadImage(image, "image/png");
            axios({
                method: "post",
                url: "http://10.0.0.68:8080/uploadImage",
                headers: {
                    "Access-Control-Allow-Origin": "*",
                    "Content-Type": "application/json"
                },
                data: {
                    image: image
                }
            });
            console.log(image);

/*
            ctx.drawImage(
                image,
                0,
                0,
                canvasRef.current.width,
                canvasRef.current.height
            );
            const str = canvasRef.toDataURL();
            console.log(str);
            var imageData = ctx.getImageData(
                0,
                0,
                canvasRef.current.width,
                canvasRef.current.height
            );
            BarcodeReader({
                image: imageData,
//                barcode: 'code-2of5',
                barcode: 'coda-128',
                options: {}
            }).then(code => {
                console.log('['+code+']');
            }).catch(err => {
                console.log(err);
            })
*/
            drawLine(ctx, { x: 20, y: 20, x1: 20, y1: 100 });

            drawLine(ctx, { x: 50, y: 50, x1: 200, y1: 100 }, { color: 'red' });

            drawLine(ctx, { x: 300, y: 250, x1: 260, y1: 70 }, { color: 'green', width: 5 });

            drawLine(ctx, { x: 70, y: 240, x1: 160, y1: 120 }, { color: 'blue' });
            drawLine(ctx, { x: 0, y: 3, x1: 640, y1: 3 }, { color: 'green', width: 5 });
            drawLine(ctx, { x: 3, y: 0, x1: 3, y1: 480 }, { color: 'green', width: 5 });
            drawLine(ctx, { x: 0, y: 477, x1: 640, y1: 477 }, { color: 'green', width: 5 });
            drawLine(ctx, { x: 637, y: 0, x1: 637, y1: 480 }, { color: 'green', width: 5 });
            /*
            var code = jsQR(imageData.data, imageData.width, imageData.height, {
                inversionAttempts: "dontInvert",
            });
            if (code) {
                console.log(code.data);
                setResult({ data: code.data, time: new Date() });
            }
            */
            return;
    }

    return (
        <div>
            <canvas id="canvas" className="canvas" ref={canvasRef} ></canvas>
            <Webcam id="webcam" ref={webcamRef} screenshotFormat="image/png" width={640} height={480}
                videoConstraints={videoConstraints}
            />
            <div>Test</div>
        </div>
    );
}

export default Reader;
